.btn-delete {
    background-color: lightgray;
    color: #fff;
    padding: 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.cart {
    /* width: 100%;
    height: 100%; */
    position: relative;
}

.cart__link {
    color: #fff;
    text-decoration: none;
    display: flex;
    gap: 10px;
    align-items: center;
}

.shopping-cart {
    display: none;
    color: black;
    text-align: center;
    min-width: 200px;
    background-color: #fff;
    /* border: 1px solid black; */
    border-radius: 10px;
    gap: 10px;
    position: absolute;
    top: 100%;
    /* left: 50%; */
    right: 0;
    /* transform: translateX(-50%); */
    z-index: 1000;
    overflow: hidden;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.1s linear;
}

.cartCounter {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
}

.product {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.shopping-cart__link {
    text-decoration: none;
    justify-self: center;
    padding: 5px 10px;
    margin-bottom: 10px;
    border-radius: 10px;
    border: 1px solid #00c8c8;
    color: #fff;
    background-color: #00c8c8;
    transition: all 0.2s ease-in-out;
}

.shopping-cart__link:hover {
    color: #00c8c8;
    background-color: #fff;
}

.shopping-cart__title {
    padding: 10px;
    margin: 0;
    background-color: #868385;
    color: #ddd;
}

@media screen and (min-width: 1024px) {
    .shopping-cart {
        display: grid;
    }
    .cart:hover .shopping-cart {
        visibility: visible;
        opacity: 1;
    }
}

@media (min-width: 1200px) {
    .btn-delete {
        background-color: lightgray;
        transition: all 0.2s linear;
    }
    .btn-delete:hover {
        background-color: red;
    }
    .shopping-cart {
        left: 50%;
        transform: translateX(-50%);
    }
}